import Head from "next/head";
import Icon from "../helper/Icon";
export default () => (
  <ex>
    <header>
      <div className="logo">1</div>

      <div className="header_bar">
        <div>
          <div>
            <Icon name="price" width={35} /> В корзине
          </div>
          <div>
            <Icon name="find" width={35} />В корзине
          </div>
        </div>

        <div>
          <Icon name="basket" width={35} />
        </div>
        <div>
          <Icon name="order" width={35} />
        </div>
        <div>
          <Icon name="card" width={35} />
        </div>
      </div>
    </header>
    <style jsx>{`
      header {
        display: flex;
        justify-content: center;
        background-color: red;
        width: 33%;
        margin: auto;
        border-radius: 5px;
        height: 10vh;
      }
      .logo {
         {
          /* display: flex;
          flex-direction: row */
        }
        margin-inline-end: auto;
      }
      .header_bar {
        display: flex;
      }
      .header_bar > div {
        display: flex;
        flex-direction: column;
      }
    `}</style>
  </ex>
);



